import React, { useEffect, useState } from 'react';
import { defaultSettings } from './store/initialState';
import { useDispatch, useSelector } from 'react-redux'
import { TextField, InputAdornment, Button, InputLabel, Typography, Select } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import { titleChanged } from './store/view';
import { allSettingsChanged } from './store/settings'
import makeOnChanged from './utils/makeOnChanged';
import makeOnBlur from './utils/makeOnBlur'
import {
  // LocalHospitalOutlined as LocalHospitalOutlinedIcon,
  // Restaurant as RestaurantIcon,
  Cake as CakeIcon,
  Maximize as MaximizeIcon,
  ShowChart as ShowChartIcon,
  FitnessCenter as FitnessCenterIcon,
  Settings as SettingsIcon,
  RadioButtonUnchecked as RadioButtonUncheckedIcon,
  DataUsage as DataUsageIcon,
  Info as InfoIcon
} from '@material-ui/icons'
import { langs } from './lang/langs';
import makeOnFocusSelect from './utils/makeOnFocusSelect';

export const Settings = () => {
  const settings = useSelector(state => state.settings)
  const lang = langs[settings.lang]
  const dispatch = useDispatch()

  const onKeyUp = (event) => {
    if (event.keyCode !== 13) return;
    event.target.blur()
  }
  const onFocusSelect = makeOnFocusSelect();
  const onChanged = makeOnChanged(settings, defaultSettings, dispatch, allSettingsChanged, { noui: false })
  const onBlur = makeOnBlur(defaultSettings, dispatch, allSettingsChanged, { noui: false })

  useEffect(() => {
    dispatch(
      titleChanged('Settings')
    )
  })


  const [showDefaultCurrentBg, setShowDefaultCurrentBg] = useState(false)
  const [showDefaultTargetBg, setShowDefaultTargetBg] = useState(false)
  const [showDefaultUnitAdjustmentPercentage, setShowDefaultUnitAdjustmentPercentage] = useState(false)
  const [showDefaultCarbsPerUnit, setShowDefaultCarbsPerUnit] = useState(false)
  const [showDefaultBgPerUnit, setShowDefaultBgPerUnit] = useState(false)
  const [showDefaultColumnWeight, setShowDefaultColumnWeight] = useState(false)
  const [showDefaultTotalPacketWeight, setShowDefaultTotalPacketWeight] = useState(false)
  const [showPortionRatio, setShowPortionRatio] = useState(false)
  const [showDefaultMinimumCarbs, setShowDefaultMinimumCarbs] = useState(false)
  const [showDefaultMinimumUnits, setShowDefaultMinimumUnits] = useState(false)

  return (
    <section className="settings screen__container">

      <div className="settings_input input__group">

        <Typography className="section__divider" variant="h6" noWrap>
          {lang.settingsInsulinUnitsTitle}
        </Typography>

        <div className="settings__input__currentbg input__container">
          <TextField label={<div>
            {lang.settingsDefaultCurrentBgLabelText}
            {lang.settingsDefaultCurrentBgInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowDefaultCurrentBg(!showDefaultCurrentBg)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showDefaultCurrentBg ? 'flex':'none'}} onClose={() => setShowDefaultCurrentBg(false)}>
                {lang.settingsDefaultCurrentBgInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'currentBg')}
            onChange={onChanged.bind(this, 'currentBg')}
            onBlur={onBlur.bind(this, 'currentBg')}
            value={settings.ui_currentBg}
            InputProps={{
              startAdornment: <InputAdornment position="start"><ShowChartIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">mmol/l</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__unitadjustmentpercentage input__container">
          <TextField label={<div>
            {lang.settingsDefaultUnitAdjustmentPercentageLabelText}
            {lang.settingsDefaultUnitAdjustmentPercentageInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowDefaultUnitAdjustmentPercentage(!showDefaultUnitAdjustmentPercentage)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showDefaultUnitAdjustmentPercentage ? 'flex':'none'}} onClose={() => setShowDefaultUnitAdjustmentPercentage(false)}>
                {lang.settingsDefaultUnitAdjustmentPercentageInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'unitAdjustmentPercentage')}
            onChange={onChanged.bind(this, 'unitAdjustmentPercentage')}
            onBlur={onBlur.bind(this, 'unitAdjustmentPercentage')}
            value={settings.ui_unitAdjustmentPercentage}
            InputProps={{
              startAdornment: <InputAdornment position="start"><FitnessCenterIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">%</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__targetbg input__container">
          <TextField label={<div>
            {lang.settingsDefaultTargetBgLabelText}
            {lang.settingsDefaultTargetBgInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowDefaultTargetBg(!showDefaultTargetBg)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showDefaultTargetBg ? 'flex':'none'}} onClose={() => setShowDefaultTargetBg(false)}>
                {lang.settingsDefaultTargetBgInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'targetBg')}
            onChange={onChanged.bind(this, 'targetBg')}
            onBlur={onBlur.bind(this, 'targetBg')}
            value={settings.ui_targetBg}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">mmol/l</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__carbsperunit input__container">
          <TextField label={<div>
            {lang.settingsDefaultCarbsPerUnitLabelText}
            {lang.settingsDefaultCarbsPerUnitInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowDefaultCarbsPerUnit(!showDefaultCarbsPerUnit)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showDefaultCarbsPerUnit ? 'flex':'none'}} onClose={() => setShowDefaultCarbsPerUnit(false)}>
                {lang.settingsDefaultCarbsPerUnitInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'carbsPerUnit')}
            onChange={onChanged.bind(this, 'carbsPerUnit')}
            onBlur={onBlur.bind(this, 'carbsPerUnit')}
            value={settings.ui_carbsPerUnit}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__bgperunit input__container">
          <TextField label={<div>
            {lang.settingsDefaultBgPerUnitLabelText}
            {lang.settingsDefaultBgPerUnitInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowDefaultBgPerUnit(!showDefaultBgPerUnit)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showDefaultBgPerUnit ? 'flex':'none'}} onClose={() => setShowDefaultBgPerUnit(false)}>
                {lang.settingsDefaultBgPerUnitInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'bgPerUnit')}
            onChange={onChanged.bind(this, 'bgPerUnit')}
            onBlur={onBlur.bind(this, 'bgPerUnit')}
            value={settings.ui_bgPerUnit}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">mmol/l</InputAdornment>,
            }}
          />
        </div>

        <Typography className="section__divider" variant="h6" noWrap>
          {lang.settingsCarbsPerMealWeightTitle}
        </Typography>

        <div className="settings__input__columnweight input__container">
          <TextField label={<div>
            {lang.settingsDefaultColumnWeightLabelText}
            {lang.settingsDefaultColumnWeightInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowDefaultColumnWeight(!showDefaultColumnWeight)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showDefaultColumnWeight ? 'flex':'none'}} onClose={() => setShowDefaultColumnWeight(false)}>
                {lang.settingsDefaultColumnWeightInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'columnWeight')}
            onChange={onChanged.bind(this, 'columnWeight')}
            onBlur={onBlur.bind(this, 'columnWeight')}
            value={settings.ui_columnWeight}
            InputProps={{
              startAdornment: <InputAdornment position="start"><MaximizeIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__totalpacketweight input__container">
          <TextField label={<div>
            {lang.settingsDefaultTotalPacketWeightLabelText}
            {lang.settingsDefaultTotalPacketWeightInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowDefaultTotalPacketWeight(!showDefaultTotalPacketWeight)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showDefaultTotalPacketWeight ? 'flex':'none'}} onClose={() => setShowDefaultTotalPacketWeight(false)}>
                {lang.settingsDefaultTotalPacketWeightInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'totalPacketWeight')}
            onChange={onChanged.bind(this, 'totalPacketWeight')}
            onBlur={onBlur.bind(this, 'totalPacketWeight')}
            value={settings.ui_totalPacketWeight}
            InputProps={{
              startAdornment: <InputAdornment position="start"><CakeIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
          />
        </div>

        <div className="proportionatecarbs__input__portionratio input__container">
          <InputLabel htmlFor="portionratio">{lang.settingsDefaultPortionRatioLabelText}</InputLabel>
          <div className="select__container">
          <Select
            native
            value={settings.ui_portionRatio}
            onChange={onChanged.bind(this, 'portionRatio')}
            onBlur={onBlur.bind(this, 'portionRatio')}
            label={<div>
            {lang.settingsDefaultPortionRatioLabelText}
            {lang.settingsDefaultPortionRatioInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowPortionRatio(!showPortionRatio)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showPortionRatio ? 'flex':'none'}} onClose={() => setShowPortionRatio(false)}>
                {lang.settingsDefaultPortionRatioInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            inputProps={{
              name: 'portionratio',
              id: 'portionratio',
            }}
            startAdornment={<InputAdornment position="start"><DataUsageIcon /></InputAdornment>}
          >
            <option aria-label="None" value={1}>Whole</option>
            <option value={1/2}>1/2 Half</option>
            <option value={1/3}>1/3 One third</option>
            <option value={1/4}>1/4 One quarter</option>
            <option value={1/5}>1/5 One fifth</option>
            <option value={1/6}>1/6 One sixth</option>
            <option value={1/8}>1/8 One eighth</option>
            <option value={2}>2 Two</option>
            <option value={2/3}>2/3 Two thirds</option>
            <option value={2/5}>2/5 Two fifths</option>
            <option value={3}>3 Three</option>
            <option value={3/5}>3/5 Three fifths</option>
            <option value={3/4}>3/4 Three quarters</option>
            <option value={4}>4 Four</option>
            <option value={4/5}>4/5 Four fifths</option>
            <option value={5}>5 Five</option>
            <option value={5/6}>5/6 Five sixths</option>
          </Select>
          </div>
        </div>

        <Typography className="section__divider" variant="h6" noWrap>
          {lang.settingsRoundingTitle}
        </Typography>

        <div className="settings__input__minimumcarbs input__container">
          <TextField label={<div>
            {lang.settingsDefaultMinimumCarbsLabelText}
            {lang.settingsDefaultMinimumCarbsInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowDefaultMinimumCarbs(!showDefaultMinimumCarbs)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showDefaultMinimumCarbs ? 'flex':'none'}} onClose={() => setShowDefaultMinimumCarbs(false)}>
                {lang.settingsDefaultMinimumCarbsInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'minimumCarbs')}
            onChange={onChanged.bind(this, 'minimumCarbs')}
            onBlur={onBlur.bind(this, 'minimumCarbs')}
            value={settings.ui_minimumCarbs}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">g</InputAdornment>,
            }}
          />
        </div>

        <div className="settings__input__minimumunits input__container">
          <TextField label={<div>
            {lang.settingsDefaultMinimumUnitsLabelText}
            {lang.settingsDefaultMinimumUnitsInfoText &&
            <React.Fragment>
              <Button className="btn-info" onClick={() => setShowDefaultMinimumUnits(!showDefaultMinimumUnits)}>
                <InfoIcon />
              </Button>
              <Alert severity="info" style={{display: showDefaultMinimumUnits ? 'flex':'none'}} onClose={() => setShowDefaultMinimumUnits(false)}>
                {lang.settingsDefaultMinimumUnitsInfoText}
              </Alert>
            </React.Fragment>}
            </div>}
            onKeyUp={onKeyUp}
            onFocus={onFocusSelect.bind(this, 'minimumUnits')}
            onChange={onChanged.bind(this, 'minimumUnits')}
            onBlur={onBlur.bind(this, 'minimumUnits')}
            value={settings.ui_minimumUnits}
            InputProps={{
              startAdornment: <InputAdornment position="start"><SettingsIcon /></InputAdornment>,
              endAdornment: <InputAdornment position="end">unit</InputAdornment>,
            }}
          />
        </div>


      </div>

    </section>
  )
}
